@import "../../assets/scss/media";

.outerContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  margin-bottom: 1rem;

  width: 100%;
  @include md {
    width: 50%;
  }
  @include lg {
    width: 33%;
  }
}

.innerContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
