@import "../../assets/scss/media";
@import "../../assets/scss/variables";

.container {
  width: 100%;

  display: flex;
  align-items: center;
  flex-direction: column;

  & > h1 {
    text-align: center;
  }
}

.iconsContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  @include sm {
    flex-direction: row;
  }

  & > div:not(:last-child) {
    margin-bottom: 2rem;
    @include sm {
      margin-right: 4rem;
      margin-bottom: 0;
    }
  }
}

.label {
  background: $color-primary;
}
