.icon {
  position: relative;
  padding: 1.5rem;

  img {
    max-width: 7rem;
    position: relative;
  }

  &::before {
    content: " ";
    width: 100%;
    height: 100%;
    background-color: #eeee;
    display: flex;
    position: absolute;
    left: 0;
    top: 0;
    border-radius: 50rem;
  }
}
