@import "../../assets/scss/variables";
@import "../../assets/scss/media";

.outerContainer {
  width: 100%;
  display: flex;
  padding: 0.25rem;
  margin-bottom: 3rem;

  @include lg {
    width: 50%;
    padding: 0.5rem 2rem 0.5rem 2rem;
  }

  @include xl {
    width: 33%;
  }

  @include xl2 {
    width: 25%;
  }
}

.innerContainer {
  display: flex;
  flex-direction: column;
  width: 100%;

  & > p {
    text-align: justify;
    padding: 1rem 0.25rem 1rem 0.25rem;
    flex-grow: 1;
  }
}

.header {
  padding: 2rem 1rem 0.5rem 1rem;
  background-color: $color-primary;
  border-radius: 1rem;

  & > h1 {
    font-size: 1.3rem;
    letter-spacing: 1px;
    font-weight: 700;
    color: white;
  }

  & > span {
    color: white;
    margin-bottom: -7px;
  }
}
