@import "../../assets/scss/variables";
@import "../../assets/scss/media";

.outerContainer {
  padding-top: 4rem;
  position: fixed;
  display: flex;
  z-index: 999;
  width: 100%;
  left: 0;

  @include md {
    padding: 0;
  }
}

.nav {
  display: flex;
  padding: 1rem 1rem;
  margin-top: 1rem;
  width: 100%;
  justify-content: center;

  flex-direction: column;
  background-color: white;

  @include md {
    display: flex;
    flex-direction: row;
    margin-top: 0rem;
  }
}

.navOpen {
  display: flex;
}

.navClosed {
  display: none;
  @include md {
    display: flex;
  }
}

.link {
  display: flex;
  position: relative;
  padding: 0.25rem 0.5rem;
  color: $color-primary;
  text-align: center;
  cursor: pointer;
  font-family: $font-heading;

  justify-content: flex-end;
  @include md {
    justify-content: center;
  }

  &:not(:last-child) {
    @include md {
      margin-right: 0.5rem;
    }
  }

  &::after {
    content: "";
    display: flex;
    position: absolute;
    height: 1px;
    width: 0%;
    background-color: $color-primary;
    transition: width 500ms;
    bottom: 0;
    border-radius: 999px;
    right: 0;

    @include md {
      right: unset;
    }
  }

  &.active::after,
  &:hover::after {
    width: 100%;
  }

  &.avtive {
    background-color: black;
  }
}
