@import "../../assets/scss/variables";
@import "../../assets/scss/media";

.hero {
  display: flex;
  width: 100%;
  margin-top: 1rem;
  flex-direction: column-reverse;
  align-items: center;

  @include lg {
    flex-direction: row;
  }
}

.bio {
  width: 100%;
  align-self: center;
  justify-self: center;

  // @include sm {
  //   width: 80%;
  // }

  // @include lg {
  //   width: 50%;
  // }

  & > h1 {
    margin-bottom: 1rem;
    margin-top: 1rem;
    text-align: center;

    @include lg {
      margin-top: 0rem;
      text-align: center;
    }
  }

  & > p {
    text-align: center;
    width: 100%;
    max-width: 40rem;
    line-height: 200%;
    font-size: $font-body-lg;
    color: $color-gray;
    margin-left: auto;
    margin-right: auto;

    @include lg {
      flex-direction: row;
      padding-right: 3rem;
    }
  }
}

.image {
  width: 100%;
  border-radius: 2rem;

  @include sm {
    width: 80%;
  }

  @include lg {
    width: 50%;
  }
}
