@import "../../assets/scss/variables";

.link {
  display: flex;
  justify-content: center;
  padding: 0.5rem 1rem 0.5rem 1rem;
  text-decoration: none;
  color: white;
  border-radius: 0.5rem;
  width: 100%;
  background-color: $color-primary;

  &:not(:last-child) {
    margin-right: 0.5rem;
  }
}
