@import "../../assets/scss/media";
@import "../../assets/scss/variables";

.fixed {
  position: fixed;
}

.relative {
  position: relative;
}

.content {
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;

  padding-left: 1rem;
  padding-right: 1rem;

  display: flex;
  flex-direction: column;

  @include md {
    max-width: $screen-sm-min;
  }

  @include lg {
    max-width: $screen-md-min;
    flex-direction: row;
  }

  @include xl {
    max-width: $screen-lg-min;
  }

  @include xl2 {
    max-width: $screen-xl2-min;
  }
}
